body{
    margin: 0;
    padding: 0;
    background: url(../Image/peaceful.jpg) no-repeat;
    background-size: cover;
}

.box{
    width: 450px;
    background: rgba(0, 0, 0, 0.5);
    padding: 40px;
    text-align: center;
    margin: auto;
    margin-top: 5%;
    color: white;
    font-family: 'Lobster Two', cursive;

}

.box-img{
    width: 250px;
    height: 250px;
    border-radius: 50%;

}

.box h1{
    font-size: 40px;
    letter-spacing: 4px;
    font-weight: 100;
}

.box h3{
    font-size: 20px;
    letter-spacing: 3px;
    font-weight: 100;
}
.box h5{
    font-size: 15px;
    letter-spacing: 2px;
    font-weight: 100;
    text-align: center;
}
ul{
    margin: 0;
    padding: 0;
    
}

.box li{
    display: inline-block;
    margin: 6px;
    list-style: none;
}

.box li a{
    text-decoration: none;
    color: white;
    font-size: 20px;
    transition: all ease-in-out 300ms;
}

.box li a:hover{
    color: #4b7d7b;
}

.update{
    cursor: pointer;
}