body{
    padding: 0;
    margin: 0;
    background-size: cover;
}

.u-box{
    width: 450px;
    background: rgba(0, 0, 0, 0.4);
    padding: 40px;
    text-align: center;
    margin: auto;
    margin-top: 5%;
    color: white;
    font-family: 'Lobster Two', cursive;
}

.u-box img{
    width: 250px;
    height: 250px;
    border-radius: 50%;
}

.u-box a{
    color: white;
    transition: all ease-in-out 300ms;
    font-size: 20px;
}

.u-box a:hover{
    color:#4b7d7b;
}